
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading,
},
data() {
return {
balance: "",
isShadow: false,
isPwd: '',
};
},
created() {
this.getBalance();
},
mounted(){
this.$nextTick(function() {
console.log('路由参数：',this.$route.query.sub_member_id);
});
},
methods: {
async getBalance() {
var sub_member_id='';
sub_member_id=this.$route.query.sub_member_id;
console.log('sub_member_id',sub_member_id)



let data = {
plat: 3,
token:this.token,
account:this.account,
sub_member_id:sub_member_id
};

const [err, res] = await api.getBalance(data);
if (err) {
console.log("err", err);
return;
}else if(res.code==2000){
console.log(res.data)
this.balance=res.data.rebate_amout;
var isPwd=res.data.is_paypwd;
this.isPwd=isPwd;
if (!isPwd) {
console.log('isPwd是false')
this.shadowToggle();
return;
}
}else{
console.log(res.code)
}


},
shadowToggle () {
this.isShadow=true;
},
withdraw(){
var isPwd=this.isPwd;
if (!isPwd) {
this.shadowToggle();
return;
}else{
var sub_member_id='';
sub_member_id=this.$route.query.sub_member_id;

this.$router.push({//跳路由
path: '/drawCash',
query: {
sub_member_id:sub_member_id
}
});
}
},
goPage(){
this.$router.push('./moblieCodeTrade');
},
cancelshadowToggle() {
this.isShadow=false;
},
linkToPage(path){
this.$router.push({
path
})
}
},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
},

};



